.banner{
    color: white;
    object-fit: contain;
    height: 448px;
}

.banner__contents{
    margin-left: 30px;
    padding-top: 140px;
    height: 190px;

}
.banner__title{
    font-size: 3rem;
    font-weight: 0.3rem;
    padding-bottom: 0.3rem;

}
.banner__description{
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 0.8rem;
    max-width: 360px;
    height: 80px;

}

.banner__button{
    cursor: pointer; 
    color: white;

    outline: none; 
    border: none; 

    font-weight: 700;
     border-radius: 0.2vw;

    padding-left: 2rem; 
    padding-right: 1rem;

    margin-right: 1rem; 
    padding-top: 0.5rem;

    background-color: rgba (51,51,51,0.5);
     padding-bottom: 0.5rem;
}

.banner__button:hover{
    color: black;
    background-color: wheat;
    transition: all 0.2s;
}

/* this is what a front end developer does
its these suttle changes that makes the web site 
look more cohesive
- look at inspector */
.banner--fadeBottom{
    height: 7.4rem;
    background-image: linear-gradient(
        180deg,
        transparent,
        rgba(37,37,37,0.61),
        #111

    );

}